import * as React from 'react';
import {
  JsonML,
  getTagName,
  getAttributes,
} from '@ali/4ever-utils';
import { ConvertFunc } from '@ali/4ever-cangjie';

export default {
  name: 'container',

  match: (node: JsonML): boolean => {
    const tag = getTagName(node);
    const attrs = getAttributes(node) as Record<string, any>;
    return tag === 'container' && attrs?.subType;
  },

  convert: (
    node: JsonML,
    path: number[],
    convert: ConvertFunc,
  ): React.ReactElement => {
    // 兜底显示空 div
    return <div>{null}</div>;
  },
};
